body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.upload-container{
    margin: 20px;
}

.browse-wrapper{
    position: relative;
    height: 50vh;    
    vertical-align: middle;
    border-style: dashed !important;
    border-width: 1px !important;
    border-radius: 3px !important;
}

.browser-wrapper-pad{
    position: absolute;
    top: 30%;
    left: 40%;
}

.browser-wrapper-pad>*{
    margin: 10px;
}

.custom-btn{
    border-style: solid;
    border-color: #c6c6c6;
}

#submit-wrapper{
    text-align: right;
    margin: 0px 20px;
}

.custom-btn-submit{
    border-style: solid;
    border-width: 1px;
    border-color: #c6c6c6;
    border-radius: 15px;
    padding: 0px 10px;
    font-weight: bold;
}
.custom-row{
    height: 60vh;
}

.col-table{
    height: 100%;
}

.choose-wrapper{
    text-align: right;
    margin: 0px 20px;
}

.custom-btn-choose{
    border-style: solid;
    border-width: 1px;
    border-color: #c6c6c6;
    border-radius: 15px;
    padding: 0px 10px;
    font-weight: bold;
}
